<template>
  <div class="container">
    <div class="header clearfix">
      <h2></h2>
    </div>
    <div class="form-wrapper">
      <el-form ref="form" :model="form" :rules="rules" size="small" label-width="150px">
        <div class="form-item">
          <h3><img src="../static/img/open-base.png" /></h3>
          <div class="context">
            <el-form-item label="" prop="agencyName">
              <el-input v-model="form.agencyName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="areaCode">
              <el-select v-model="form.areaCode" placeholder="">
                <el-option v-for="(area,index) in areas" :label="area" :value="area" :key="index"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="applyName">
              <el-input v-model="form.applyName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="phoneNumber">
              <el-input v-model="form.phoneNumber"></el-input>
            </el-form-item>
            <el-form-item label="" prop="address">
              <el-input v-model="form.address"></el-input>
            </el-form-item>
            <el-form-item label="" prop="postalCode">
              <el-input v-model="form.postalCode"></el-input>
            </el-form-item>
          </div>
        </div>
        <div class="form-item">
          <h3><img src="../static/img/open-regist.png" /></h3>
          <div class="context">
            <el-form-item label="" prop="businessRegistrationName">
              <el-input v-model="form.businessRegistrationName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="organizationCode">
              <el-input v-model="form.organizationCode"></el-input>
            </el-form-item>
            <el-form-item label="" prop="businessLicenseNo">
              <el-input v-model="form.businessLicenseNo"></el-input>
            </el-form-item>
            <el-form-item label="" prop="taxRegistrationNumber">
              <el-input v-model="form.taxRegistrationNumber"></el-input>
            </el-form-item>
            <el-form-item label="" prop="businessScope">
              <el-input v-model="form.businessScope"></el-input>
            </el-form-item>
            <el-form-item label="()" prop="registeredCapital">
              <el-input v-model="form.registeredCapital"></el-input>
            </el-form-item>
          </div>
        </div>
        <div class="form-item">
          <h3><img src="../static/img/open-account.png" /></h3>
          <div class="context">
            <el-form-item label="" prop="mainAccountBank">
              <el-select v-model="form.mainAccountBank" placeholder="">
                <el-option v-for="(bank,index) in banklists" :label="bank.bankName" :key="index" :value="bank.bankCode"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="bankName">
              <el-input v-model="form.bankName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="bankAccountName">
              <el-input v-model="form.bankAccountName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="bankAccount">
              <el-input v-model="form.bankAccount"></el-input>
            </el-form-item>
            <el-form-item label="" prop="legalPersonName">
              <el-input v-model="form.legalPersonName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="corporateIDNumber">
              <el-input v-model="form.corporateIDNumber"></el-input>
            </el-form-item>
          </div>
        </div>
        <div class="form-item">
          <h3><img src="../static/img/open-rate.png" /></h3>
          <div class="context">
            <el-form-item label="(%)" prop="wechatRate">
              <el-input v-model="form.wechatRate"></el-input>
              <span class="tips">(0.30.)</span>
            </el-form-item>
            <el-form-item label="(%)" prop="alipayRate">
              <el-input v-model="form.alipayRate"></el-input>
              <span class="tips">(0.30.)</span>
            </el-form-item>
            <el-form-item label="(%)" prop="alipayServiceRate">
              <el-input v-model="form.alipayServiceRate"></el-input>
              <span class="tips">(0.30.)</span>
            </el-form-item>
            <el-form-item label="(%)" prop="weChatPublicRate">
              <el-input v-model="form.weChatPublicRate"></el-input>
              <span class="tips">(0.30.)</span>
            </el-form-item>
            <el-form-item label="QQ(%)" prop="qqRate">
              <el-input v-model="form.qqRate"></el-input>
              <span class="tips">(0.30.)</span>
            </el-form-item>
            <el-form-item label="(%)" prop="gatewayRate">
              <el-input v-model="form.gatewayRate"></el-input>
              <span class="tips">(0.30.)</span>
            </el-form-item>
            <el-form-item label="(%)" prop="quickRate">
              <el-input v-model="form.quickRate"></el-input>
              <span class="tips">(0.30.)</span>
            </el-form-item>
            <el-form-item label="WAP(%)" prop="wapRate">
              <el-input v-model="form.wapRate"></el-input>
              <span class="tips">(0.30.)</span>
            </el-form-item>
          </div>
        </div>
        <div class="form-item">
          <h3><img src="../static/img/open-connect.png" /></h3>
          <div class="context">
            <el-form-item label="" prop="businessContact">
              <el-input v-model="form.businessContact"></el-input>
            </el-form-item>
            <el-form-item label="" prop="businessContactPhone">
              <el-input v-model="form.businessContactPhone"></el-input>
            </el-form-item>
            <el-form-item label="QQ" prop="businessContactQQ">
              <el-input v-model="form.businessContactQQ"></el-input>
            </el-form-item>
            <el-form-item label="Email" prop="businessContactEmail">
              <el-input v-model="form.businessContactEmail"></el-input>
            </el-form-item>
            <el-form-item label="" prop="businessContactFax">
              <el-input v-model="form.businessContactFax"></el-input>
            </el-form-item>
          </div>
        </div>
        <div class="form-btn">
          <el-form-item size="small">
            <el-button type="primary" @click="onSubmit"></el-button>
            <el-button></el-button>
          </el-form-item>
        </div>
      </el-form>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  import {
    formatMoney,
    checkPhone,
    checkPostalCode,
    checkAllNum,
    checkBankCardAttribution,
    checkRate,
    checkQQ,
    checkEmail,
    checkFax
  } from 'static/js/util'
  import { BankList } from 'static/js/bankList'

  export default {
    data () {
      return {
        areas: ['area1', 'area2', 'area3', 'area4', 'area5'],
        banklists: [],
        form: {
          agencyName: '',
          areaCode: '',
          applyName: '',
          phoneNumber: '',
          address: '',
          postalCode: '',
          businessRegistrationName: '',
          organizationCode: '',
          businessLicenseNo: '',
          taxRegistrationNumber: '', // 
          businessScope: '',
          registeredCapital: '', // 
          mainAccountBank: '',
          bankAccountName: '',
          bankName: '',
          bankAccount: '',
          legalPersonName: '',
          corporateIDNumber: '',
          wechatRate: '',
          alipayRate: '',
          alipayServiceRate: '',
          weChatPublicRate: '',
          qqRate: '',
          gatewayRate: '',
          quickRate: '',
          wapRate: '',
          businessContact: '',
          businessContactPhone: '',
          businessContactQQ: '',
          businessContactEmail: '',
          businessContactFax: ''
        },
        rules: {
          agencyName: [
            {required: true, message: '', trigger: 'blur'},
            {min: 3, max: 6, message: ' 3  6 ', trigger: 'blur'}
          ],
          areaCode: [
            {required: true, message: '', trigger: 'blur'}
          ],
          applyName: [
            {required: true, message: '', trigger: 'blur'}
          ],
          phoneNumber: [
            {required: true, message: '', trigger: 'blur'},
            {validator: checkPhone, trigger: 'blur'}
          ],
          address: [
            {required: true, message: '', trigger: 'blur'}
          ],
          postalCode: [
            {required: true, message: '', trigger: 'blur'},
            {validator: checkPostalCode, trigger: 'blur'}
          ],
          businessRegistrationName: [
            {required: true, message: '', trigger: 'blur'}
          ],
//          organizationCode: [
//            {required: true, message: '', trigger: 'blur'}
//          ],
//          businessLicenseNo: [
//            {required: true, message: '', trigger: 'blur'}
//          ],
//          taxRegistrationNumber: [
//            {required: true, message: '', trigger: 'blur'}
//          ],
          businessScope: [
            {required: true, message: '', trigger: 'blur'}
          ],
          registeredCapital: [
            {required: true, message: '', trigger: 'blur'},
            {validator: checkAllNum, trigger: 'blur'}
          ],
          mainAccountBank: [
            {required: true, message: '', trigger: 'blur'}
          ],
          bankName: [
            {required: true, message: '', trigger: 'blur'}
          ],
          bankAccountName: [
            {required: true, message: '', trigger: 'blur'}
          ],
          bankAccount: [
            {required: true, message: '', trigger: 'blur'}
          ],
          legalPersonName: [
            {required: true, message: '', trigger: 'blur'}
          ],
          corporateIDNumber: [
            {required: true, message: '', trigger: 'blur'}
          ],
          wechatRate: [
            {required: true, message: '', trigger: 'blur'},
            {validator: checkRate, trigger: 'blur'}
          ],
          alipayRate: [
            {required: true, message: '', trigger: 'blur'},
            {validator: checkRate, trigger: 'blur'}
          ],
          alipayServiceRate: [
            {required: true, message: '', trigger: 'blur'},
            {validator: checkRate, trigger: 'blur'}
          ],
          weChatPublicRate: [
            {required: true, message: '', trigger: 'blur'},
            {validator: checkRate, trigger: 'blur'}
          ],
          qqRate: [
            {required: true, message: 'QQ', trigger: 'blur'},
            {validator: checkRate, trigger: 'blur'}
          ],
          gatewayRate: [
            {required: true, message: '', trigger: 'blur'},
            {validator: checkRate, trigger: 'blur'}
          ],
          quickRate: [
            {required: true, message: '', trigger: 'blur'},
            {validator: checkRate, trigger: 'blur'}
          ],
          wapRate: [
            {required: true, message: 'WAP', trigger: 'blur'},
            {validator: checkRate, trigger: 'blur'}
          ],
          businessContact: [
            {required: true, message: '', trigger: 'blur'}
          ],
          businessContactPhone: [
            {required: true, message: '', trigger: 'blur'},
            {validator: checkPhone, trigger: 'blur'}
          ],
          businessContactQQ: [
            {required: true, message: 'QQ', trigger: 'blur'},
            {validator: checkQQ, trigger: 'blur'}
          ],
          businessContactEmail: [
            {validator: checkEmail, trigger: 'blur'}
          ],
          businessContactFax: [
            {validator: checkFax, trigger: 'blur'}
          ]
        }
      }
    },
    created () {
      this.banklists = BankList
      this.form.wechatRate = formatMoney('0.4')
      const {bankName, bankCode, cardType} = checkBankCardAttribution('6226 6222 0674 6683')
      console.log(bankName, bankCode, cardType === 'DC' ? '' : '')
      this.form.mainAccountBank = bankName
    },
    methods: {
      onSubmit () {
        console.log('submit', this.form)
      }
    }
  }
</script>
<style>
</style>



