<template>
  <div class="announcement">
    <h2>{{title}}</h2>
    <div class="container">
      <p class="greetings"></p>
      <p class="context">{{content}}</p>
      <p class="right">ID{{id}}</p>
      <p class="right">{{createTime}}</p>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
    export default {
      data () {
        return {
          id: '',
          title: '',
          content: '',
          createTime: '2017-10-05',
          readed: true
        }
      },
      created () {
        console.log(this.$route)
        this.id = this.$route.query.id
      }
    }
</script>
<style scoped>
  .announcement h2{
    padding: 30px 0 20px;
    color: #333;
    font-size: 16px;
    text-align: center;
  }
  .container{
    font-size: 14px;
    color: #4d4d4d;
    padding: 0 40px;
  }
  .container .greetings{
    padding: 0 0 10px;
  }
  .container .context{
    text-indent: 20px;
    padding: 10px 0 40px;
    line-height: 24px;
  }
  .container .right{
    text-align: right;
    line-height: 30px;
  }
</style>



