<template>
  <div class="dashboard">
    <InfoList v-for="(item, index) in dashboardData"
              :key="index"
      :title="item.title"
      :icon="item.icon"
      :list="item.list"></InfoList>
  </div>
</template>
<script type="text/ecmascript-6">
  import InfoList from 'components/InfoList'
  import userinfoIcon from 'static/img/icon-userinfo.png'
  import dataIcon from 'static/img/icon-data.png'
  import {formatMoney} from 'static/js/util'
  export default {
    data () {
      return {
        dashboardData: [
          {
            title: '',
            icon: userinfoIcon,
            list: [
              {
                tit: '',
                txt: ''
              },
              {
                tit: '',
                txt: ''
              },
              {
                tit: '',
                txt: '13456789389'
              },
              {
                tit: '',
                txt: 'viiv_lgr@163.com'
              },
              {
                tit: '',
                txt: `<img src="${'static/img/icon-userinfo.png'}" />`
              }
            ]
          },
          {
            title: '',
            icon: dataIcon,
            list: [
              {
                tit: '',
                txt: '0'
              },
              {
                tit: '',
                txt: '0.00'
              }
            ]
          }
        ]
      }
    },
    created () {
      const num = 10
      const money = formatMoney(1234567890)
      this.dashboardData[1].list[0].txt = num + ''
      this.dashboardData[1].list[1].txt = money + ''
    },
    components: {
      InfoList
    }
  }
</script>
<style>
</style>



