<template>
  <div class="container">
    <div class="header clearfix">
      <h2></h2>
      <div class="right">
        <span class="right-info"> {{tradeAmount}}</span>
        <span class="right-info"> {{tradeAmountNum}}</span>
        <el-button type="primary" size="small"></el-button>
      </div>
    </div>
    <div class="table">
      <el-table
        :data="tableData"
        size="mini"
        show-overflow-tooltip
        header-cell-class-name="table-th"
        style="width: 100%">
        <el-table-column
          v-for="(item,index) in table"
          :key="index"
          :prop="item.prop"
          :label="item.label"
          header-align="center"
          align="center"
        ></el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  import { formatMoney } from 'static/js/util'
  export default {
    data () {
      return {
        tradeAmount: 100,
        tradeAmountNum: 100,
        table: [
          {
            prop: 'orderId',
            label: ''
          },
          {
            prop: 'storeId',
            label: ''
          },
          {
            prop: 'storeName',
            label: ''
          },
          {
            prop: 'agencyId',
            label: 'ID'
          },
          {
            prop: 'tradeMoney',
            label: ''
          },
          {
            prop: 'tradeStatus',
            label: ''
          },
          {
            prop: 'tradeWay',
            label: ''
          },
          {
            prop: 'orderStatus',
            label: ''
          },
          {
            prop: 'orderStatus',
            label: ''
          }
        ],
        tableData: [
          {
            orderId: 'order123412',
            storeId: 'store12313',
            storeName: '',
            agencyId: 'agency123123',
            tradeMoney: '12313.00',
            tradeStatus: '',
            tradeWay: '',
            orderStatus: '',
            date: '2017-11-09'
          },
          {
            orderId: 'order123412',
            storeId: 'store12313',
            storeName: '',
            agencyId: 'agency123123',
            tradeMoney: '12313.00',
            tradeStatus: '',
            tradeWay: '',
            orderStatus: '',
            date: '2017-11-09'
          },
          {
            orderId: 'order123412',
            storeId: 'store12313',
            storeName: '',
            agencyId: 'agency123123',
            tradeMoney: '12313.00',
            tradeStatus: '',
            tradeWay: '',
            orderStatus: '',
            date: '2017-11-09'
          },
          {
            orderId: 'order123412',
            storeId: 'store12313',
            storeName: '',
            agencyId: 'agency123123',
            tradeMoney: '12313.00',
            tradeStatus: '',
            tradeWay: '',
            orderStatus: '',
            date: '2017-11-09'
          }
        ]
      }
    },
    created () {
      this.tradeAmount = formatMoney(100)
      this.tradeAmountNum = 100
    }
  }
</script>



